import React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
import FaqHero from "../images/rewarded-video-image.png"
import CurvedEdge from "../images/edge-1.png"

import SEO from "../components/seo"

const IndexPage = () => (
  <Layout>
    <SEO title="Wattpad Rewards" />
    <div className="section tall orange-bg" id="section-one">
      <div className="inner-content smed">
        <div className="row pad-lt ">
          <div className="double-flex">
            
            <div id="image-container">
              <div id="image-container-inner" className="sub-hero">
                <h1 className="white-text h1-larger-text" id="header-text-sub">Earn Coins to unlock Paid Stories</h1>
                <img src={FaqHero} id="hero-bg" className="m-view m-hide"/>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div className="section pad-t orange-bg" id="section-two">
      <div className="inner-content">
          <div className="row a-center smed pad-lr">
            <div className="double-flex">
              <h1 className="reg-text beige-text">
                Get Coins to unlock Paid Stories and support writers.
              </h1>
              <h1 className="light-text beige-text">
                You help writers earn money for their work when you redeem Coins to read Paid Stories. Now you can get Coins in two ways: purchase Coins through your Apple or Google Play account in the Coin Shop, or earn Coins through watching videos. 
              </h1>
            </div>
          </div>
      </div>
    </div>


    <div id="curve-test1" className="curve-sub">
      <img src={CurvedEdge} />
    </div>




    <div className="section sub-section" id="section-three-faq">
      <div className="inner-content med pad-b">
        <div className="col a-left smed pad-lr">
          <div className="double-flex">
            <h2 className="charcoal question">Q: How do I earn Coins?</h2>
            <div className="answer-wrapper">
              <p className="charcoal pad-sb light-text">A: You can earn Coins by watching video ads. To earn Coins visit the Earn tab in the Coin shop.</p>
              <p className="charcoal pad-sb light-text">When you earn Coins the balance in your wallet will be updated and you can use Coins to unlock Paid Stories wherever you use Wattpad. </p>
            </div>
          </div>
          
          <div className="double-flex">
            <h2 className="charcoal question">Q: How can I purchase Coins if I would like to unlock more chapters at one time? </h2>
            <div className="answer-wrapper">
              <p className="charcoal pad-sb light-text">A: You can purchase Coins to unlock multiple chapters or full stories in the Coin Shop. Learn more about how to purchase and unlock Paid Stories <a href="https://www.wattpad.com/paidstories/"  target="_blank">here</a>.</p>
            </div>
          </div>
          
          <div className="double-flex">
            <h2 className="charcoal question">Q: Why did Wattpad add Coins?</h2>
            <div className="answer-wrapper">
              <p className="charcoal pad-sb light-text">A: Coins are used to unlock Paid Stories. Wattpad Paid Stories is a program that makes it possible for writers to earn money for their work, while providing readers with a way to monetarily support writers on Wattpad.</p>
              <p className="charcoal pad-sb light-text">Readers can purchase or earn Coins that can then be used to unlock chapters or full stories that are part of the program.</p>
            </div>
          </div>
          
          <div className="double-flex">
            <h2 className="charcoal question">Q: What can I use Coins for?</h2>
            <div className="answer-wrapper">
            <p className="charcoal pad-sb light-text">A: Coins are used to unlock Paid Stories. You can learn more about Paid Stories <a href="https://www.wattpad.com/paidstories/"  target="_blank">here</a>. </p>
            </div>
          </div>
          
          <div className="double-flex">
            <h2 className="charcoal question">Q: Why is there a limit on Coins I can earn?</h2>
            <div className="answer-wrapper">								
              <p className="charcoal pad-sb light-text">A: We would like all Wattpadders to have access to Paid Stories. Earning Coins is a way for the entire community to participate. A daily personal allowance makes it possible for all Wattpadders to earn Coins equally. There are a set number of ads available, and we want to ensure the community shares access. </p>
            </div>
          </div>
          
          <div className="double-flex">
            <h2 className="charcoal question">Q: Do writers still make money when I use Coins I earn to unlock Paid Stories?</h2>
            <div className="answer-wrapper">
              <p className="charcoal pad-sb light-text">A: Absolutely! The goal of the Paid Stories program is for writers to earn money for their work.</p>
              <p className="charcoal pad-sb light-text">When you watch ads you are earning money to share with writers. </p>

            </div>
          </div>				
                
          <div className="double-flex">
            <h2 className="charcoal question">Q: Why is this feature not available in all countries</h2>
            <div className="answer-wrapper">
              <p className="charcoal pad-sb light-text">A: We are currently testing the best way for Wattpadders to earn Coins. We are releasing this program as a test to half of the Wattpad community  in the United States, Canada, Australia, and New Zealand. </p>
              <p className="charcoal pad-sb light-text">This will help us understand how our community interacts with the new feature, which will help us determine the best way to build this feature for the broader Wattpad community. </p>

            </div>
          </div>	
          
          <div className="double-flex">
            <h2 className="charcoal question">Q: Do I still earn Coins if I watch a portion of the ad?</h2>
            <div className="answer-wrapper">
              <p className="charcoal pad-sb light-text">A: No. In order to earn Coins you need to watch the entire duration of the video ad. </p>
            </div>
          </div>
                              

          
          <div className="double-flex">
            <h2 className="charcoal question">Q: Is there a cap on the number of Coins you can accumulate and spend at one time?</h2>
            <div className="answer-wrapper">
              <p className="charcoal pad-sb light-text">A: Yes. Wattpadders can save up to 30 Coins in their wallet. Coins are used to unlock Paid Stories, which helps writers earn money for their work. Capping the limit helps ensure the Coins are benefitting writers.</p>
              <p className="charcoal pad-sb light-text">The way you redeem your Coins is up to you. You can use as many Coins as you like daily.</p>
            </div>
          </div>
          
          <div className="double-flex">
            <h2 className="charcoal question">Q: I see that I have reached my daily limit of earned Coins. When is the day reset? </h2>
            <div className="answer-wrapper">
              <p className="charcoal pad-sb light-text">A: You can earn up to 3 Coins a day. Everyday at 00:00 UTC the Coin Shop is reset and you are able to start earning Coins again.</p>
              <p className="charcoal pad-sb light-text">This means the time your wallet resets changes based on the city you live in. So if you live in New York you’ll  be able to start earning Coins at 8:00PM, or 1:00AM in London, and 10:00AM in Sydney.  </p>
            </div>
          </div>

          <div className="double-flex">
            <h2 className="charcoal question">Q: Do Coins expire?</h2>
            <div className="answer-wrapper">
              <p className="charcoal pad-sb light-text">A: Yes, earned Coins expire after 3 months. Once you earn Coins you can spend them to unlock any story you wish within the 3 month period. After you unlock a chapter you will full access to it going forward. </p>
            </div>
          </div>

          <div className="double-flex">
            <h2 className="charcoal question">Q: Can I earn Coins on all platforms?</h2>
            <div className="answer-wrapper">
              <p className="charcoal pad-sb light-text">A: You can earn Coins on the Wattpad app on your Apple or Android device. Once you earn Coins you can use them to unlock Paid Stories wherever you use Wattpad, this includes mobile apps and on a web browser. </p>
            </div>
          </div>
        </div>
      </div>
    </div>

  </Layout>
)

export default IndexPage
